#getintouch{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: var(--color-white);
  
}
#getintouch h2{
  font-size: 3rem;
  text-align: center;
  padding: 1rem 0 1rem 0;
  color: #023358;

}
#getintouch h4{
font-size: 1rem;
text-align: center;
padding: 1rem 0 1rem 0;
color: #023358;
margin-bottom:10px

}
#getintouch h5{
font-size: 1.5rem;
margin-top: 2rem;
margin-bottom: 2rem;
text-align: start;
padding: 1rem 0 1rem 0;
color: #023358;
margin-left: 4rem;

}
.info-container{
border: 2px solid #b8b6b6;
background-color:#FFF;
width: 50%;
margin: 0 auto;
border-radius: 25px;
margin-bottom: 2rem;
box-shadow: 9px 9px 5px -2px rgba(0,0,0,0.58);
}
.information{
margin-left: 3rem;
display: grid;
grid-template-columns: repeat(2,12rem);
gap: 60px 180px;
margin-bottom: 2rem;
}

::placeholder{
position: absolute;
bottom: 10px;
font-size: 1rem;
}

.input-box input{
position: relative; 
text-align: start;
padding: 15px;
outline: 0;
border-bottom: 2px solid #b8b6b6 ;
width: 170%;
}


#our-serv {
margin-left: 5rem;
outline: 0;
border-bottom: 2px solid #b8b6b6 ;
width: 35%;
font-size: 1rem;
padding: 1rem 1.5rem;

}


#text-area{
margin-left: 5rem;
border-bottom: 2px solid #b8b6b6 ;
outline: none;
color: #b8b6b6;
width: 85%;
font-size: 1.2rem;
margin-bottom: 2rem;
resize: none;
}
.tellus h6{
margin-left: 40rem;
}
.budget select{
margin-left: 5rem;
outline: 0;
border-bottom: 2px solid #b8b6b6 ;
width: 35%;
font-size: 1rem;
padding: 1rem 1.5rem;
margin-bottom: 2rem;
}
.submit{
margin-left: 5rem;
display: flex;
justify-content: space-between;
width: 58%;
margin-bottom: 4rem;
margin-top: 2rem;
}
.submit button{
width: 11.25rem;
color: #024375;
height: 3.125rem;
padding: 0 1.25rem;
font-size: 1.25rem;
font-weight: 500;
border: 1px solid #fff;
border-radius: 30px;
padding: 12px 12px;
transition: ease-out 0.3s;
box-shadow: inset 0 0 0 0 #ED6239; 
}

.submit button:hover{
color: #fff;
box-shadow:inset 180px 0 0 0 #ED6239; 
cursor: pointer;
}
.submit h6{
margin-top: 1rem;
font-size: 0.75rem;
font-weight: 450;
}
.submit span{
color: #ED6239;
cursor: pointer;
}
#contact{
display: grid;
background-color: var(--color-white);
grid-template-columns: repeat(4,1fr);
justify-content: space-between;
gap: 0 20px;
align-items: center;
width: 100%;
padding-left: 1rem;
padding-right: 1rem;
padding-top: 1rem;
padding-bottom: 1rem;
margin-top: 2rem;
margin-bottom: 5rem;
}
#contact .chat-with-us{
background-color: #125488;
width: 100%;
padding: 60px 50px;
box-shadow: 5px 7px 5px -2px rgba(0,0,0,0.58);
}
.chat-with-us:hover {
cursor: pointer;
}
.chat-with-us:hover i{
transition: ease 0.4s;
transform: scale(1.5);   
}
.chat-with-us i{
color: #fff;
font-size: 2.5rem;
text-align: center;
width: 100%;
line-height: 2;
transition: ease-out 0.4s;
}
.chat-with-us h3{
color: #fff;
font-size: 1.5rem;
font-weight: 550;
line-height: 2;
text-align: center;
}
.chat-with-us p{
color: #fff;
text-align: center;

}
#contact .ask-comu{
background-color: #ED6239;
width: 100%;
padding: 60px 50px;
box-shadow: 5px 7px 5px -2px rgba(0,0,0,0.58);
cursor: pointer;
}
.ask-comu:hover i{
