line-height: 2;
transition: ease 0.4s;
}

.ask-comu h3{
color: #fff;
font-size: 1.5rem;
font-weight: 550;
line-height: 2;
text-align: center;
}
.ask-comu p{
color: #fff;
text-align: center;
}
